.search-bar {
  margin: 10px 0;
  display: flex;
}

.search-input {
  width: 250px;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  transition: 0.3s;
}

.search-input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
}
